import utils from  '../filters'
export default {
  methods: {
    countDown(end, start){
      var sys_sec = parseFloat((end - start)/1000)
      if(sys_sec > (24*3600)){
        let day = Math.ceil((sys_sec/3600)/24)
        this.countTime = day + ''
      }else if(sys_sec > 0){
        var timer = setInterval(() => {
          if(sys_sec > 1){
            sys_sec -= 1
            let day = Math.floor((sys_sec/3600)/24)
            let hour = Math.floor((sys_sec/3600)%24)
            let min = Math.floor((sys_sec/60)%60)
            let sec = Math.floor(sys_sec%60)
            let showday = day
            let showHour = hour<10?'0'+hour : hour  //
            let showMin = min<10?'0'+min : min  //
            let showSec = sec<10?'0'+sec : sec  //
            this.countTime = showHour + '' + showMin + '' + showSec + ''
          }else{
            clearInterval(timer)
            window.location.reload()
          }
        }, 1000)
      }
    },
    countDownSale(end, start){  //3
      var sys_sec = parseFloat((end - start)/1000)
      if(sys_sec > (3*3600)){ // 3
        this.countTimeSale = utils.dateFormatFun(end, 3) + ''
      }else{
        var timer = setInterval(() => {
          if(sys_sec > 1){
            sys_sec -= 1
            let day = Math.floor((sys_sec/3600)/24)
            let hour = Math.floor((sys_sec/3600)%24)
            let min = Math.floor((sys_sec/60)%60)
            let sec = Math.floor(sys_sec%60)
            let showday = day
            let showHour = hour<10?'0'+hour : hour  //
            let showMin = min<10?'0'+min : min  //
            let showSec = sec<10?'0'+sec : sec  //
            this.countTimeSale = ' '+showHour + ':' + showMin + ':' + showSec
          }else{
            clearInterval(timer)
            // window.location.reload()
          }
        }, 1000)
      }
    }
  }
}



